<template>
	<div>
		
		
		<!-- wrap -->
		<div id="wrap">
			<!-- header -->
			<header id="sub">
			<div class="header_wrap underLineGray">
				<h1 style="padding-left:5%;">공지사항</h1>
			</div>
			</header>
			<!-- header -->
			<!-- content -->
			<div id="contents">
				<div class="board_view">
					<h6>{{getNoticeDetail.noticeTitle}}</h6>
					<p class="viewarea_date">{{YYYYMMDDDate(getNoticeDetail.regDate)}}</p>
					<div class="viewarea">
						<div class="fileArea" v-if="fileExistCheck">
							<template v-for="(file,index) in getNoticeDetail.noticeFile" >
								<a v-if="file.fileType !=='PICTURE' && file.fileType !=='MOVIE'" :key="index" @click="downloadItem(file)" >{{file.fileOriginalName}}
									<br>
								</a>
							</template>
						</div>

						
						<template v-for="(file,index) in getNoticeDetail.noticeFile" >
							<img   v-if="file.fileType ==='PICTURE'" :key="index" :src="file.filePath" style="max-width:100%; height:auto;">
							<video  v-if="file.fileType ==='MOVIE'" :key="index" width="100%" height="auto" controls>
								<source v-if="file.fileExtension == '.mp4'" :src="file.filePath" type='video/mp4;'>
								<source v-if="file.fileExtension == '.ogv'" :src="file.filePath" type='video/ogg;'>
							</video>
						</template>
						
                    	<span v-html="changeTxtToHtml(getNoticeDetail.noticeContent)"></span>
					</div>
				</div>
				<div class="btn_next_area"><div class="btn_next_area_inner"><button class="btn_list rigthspace" @click="list">목록</button></div></div>
			</div>
			<!-- //content -->
			<!-- footer -->
				<Footer/>
			<!-- //footer -->
		</div>
		<!--// wrap -->
	</div>
</template>
<script>
import  Footer from '@/components/common/Footer'
import Images from "@/components/common/Images";
import  Search from '@/components/notice/Search'
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
  name: 'ObituaryReg',
	mixins: [myMixin],
  components: { 
	  Footer,
	  Search
	},
  	props: {
  	},
    data(){
        return{
			id: this.$route.query.id ? this.$route.query.id : '',
		}
	},
	computed:{
		...mapGetters(['getNoticeDetail']),
		fileExistCheck(){
			if(this.getNoticeDetail.noticeFile!==null && this.getNoticeDetail.noticeFile!==undefined && this.getNoticeDetail.noticeFile.length>0){
				for(let i=0;i<this.getNoticeDetail.noticeFile.length;i++){
					if(this.getNoticeDetail.noticeFile[i].fileType !=='PICTURE'){
						return true
					}
				}
			}
			return false
		}
	},
   	mounted() {
		   const parameter = {
			   id: this.id
		   };
		    this.$store.dispatch('REQUEST_NOTICE_DETAIL',parameter)
	},
	beforeDestroy(){
		this.$store.commit('NOTICE_INIT')
	},
	methods:{
		list(){
			
            this.$router.go(-1)
		}
	}
}
</script>

